* {
  font-family: "Bowlby One", cursive;
  text-decoration: none;
  color: white;
  -webkit-text-stroke: 1px #ae5d40;
}

h1,
h2 {
  -webkit-text-stroke: 1px #ad5d3f;
  text-align: center;
  margin: 0;
}

html,
body {
  margin: 0;
  font-family: "Bowlby One", cursive;
  height: 100%;
  background: rgb(128, 200, 179);
  background: linear-gradient(
    0deg,
    rgba(128, 200, 179, 1) 28%,
    rgba(183, 231, 177, 1) 100%
  );
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  width: 80px;
  height: 80px;
}

.header {
  background-color: #ac6950;
}

.contador {
  background-image: url("images/Estatua.png");
  background-repeat: no-repeat;
  background-size: contain;
  text-align: center;
  height: 85%;
  width: 100%;
  position: absolute;
  bottom: 0;
  background-position: center;
}

.container-title {
  margin-top: 113px;
}

.container-buttons {
  margin-top: 45px;
  text-align: center;
}

.button-axie {
  background-color: #c07f5b;
  border: 3.5px solid #5d320b;
  border-radius: 10px;
  text-decoration: none;
  color: white;
  font-family: "Bowlby One", cursive;
  font-size: 16px;
  font-weight: bold;
  width: 120px;
  height: 60px;
  margin-right: 5px;
  -webkit-text-stroke: 0.5px #9b6952;
}

.button-axie:hover {
  background-color: #ae7352;
}

.button-energie {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 5px;
  display: block;
  width: 245px;
  height: 100px;
  border-radius: 10px;
  border: 3.5px solid #5d320b;
  -webkit-text-stroke: 0px #ae5d40;
  font-size: 25px;
}

.color-green {
  background-color: #4caf50;
}

.color-green:hover {
  background-color: #429347;
}

.color-red {
  background-color: #dd4748;
}

.color-red:hover {
  background-color: #c74244;
}

.img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 40px;
  height: 40px;
}

#root {
  height: 100%;
}
.App {
  height: 100%;
}
